import { IAPI, IMockAPI, ISQLiteAPI, DaoFactory, ClientCustomDao, InRestriction, LikeRestriction, ORCompoundRestriction, EqualRestriction, IRestriction, LimitRestriction, OffsetRestriction, AndCompoundRestriction, PageInfo, OrderByRestriction, ProfileCodeService, LessOrEqualRestriction, GreaterOrEqualRestriction, NotEqualRestriction, StringUtils } from "@allianzSND/core";
import { Observable } from 'rxjs';
import { CustomerFilterCriteria } from "../components/bean/customer-filter-criteria";

export class CustomerListAPI implements IAPI, IMockAPI, ISQLiteAPI {

  private _restrictions: Array<IRestriction>;
  private _pageInfo: PageInfo = new PageInfo();

  private _queryClientID: string;

  constructor(private daoFactory: DaoFactory, private profileCodeService: ProfileCodeService) {

  }
  set clientID(clientID: string) {
    this._queryClientID = clientID;
  }

  set restrictions(restrictions: Array<IRestriction>) {
    this._restrictions = restrictions;
  }

  set pageInfo(pageInfo: PageInfo) {
    this._pageInfo = pageInfo;
  }

  getAPIName(): string {
    return 'getCustomerList';
  }

  getMockPath(): string {
    return './assets/mock/getCustomerList.json';
  }

  executeSQL(): Observable<any> {

    return Observable.create((observer) => {
      let customerObj = this.daoFactory.getDefaultTable("TW_LH_SD_VW_Customer");
      let dao = this.daoFactory.getDefaultDao();
      if (customerObj != undefined && dao != undefined) {

        dao = new ClientCustomDao(dao);

        if (this._restrictions.length > 0) {
          this._restrictions.forEach((condition: IRestriction) => {
            customerObj.addRestriction(condition);
          })
        }


        //add order by
        customerObj.addRestriction(new OrderByRestriction([{ column: 'LastName', order: 'ASC' }]))

        //add page limit
        if (this._pageInfo.pageSize > 0) {
          customerObj.addRestriction(new LimitRestriction([this._pageInfo.pageSize]));
          customerObj.addRestriction(new OffsetRestriction([(this._pageInfo.page - 1) * 10]));
        }

        //if has edit profile will has clientID
        if (StringUtils.isNotEmpty(this._queryClientID)) {
          customerObj.addRestriction(new EqualRestriction('ClientID', [this._queryClientID]));
        }

        dao.queryByTable(customerObj).subscribe((resp) => {

          observer.next(resp);
          observer.complete();
        });
      } else {
        observer.next(false);
        observer.complete();
      }
    });
  }

}
