import { Injectable, Optional, Inject } from '@angular/core';
import { CustomerImportDisplay } from '../../interface/CustomerImportDisplay';
import { customerImportDisplayToken } from '../../injectionToken/injection-token';
import { StringUtils } from '@allianzSND/core';

@Injectable({
    providedIn: 'root'
})
export class DefaultCustomerImportDisplay implements CustomerImportDisplay {

    constructor(
        @Optional() @Inject(customerImportDisplayToken) private customImportDisplay: CustomerImportDisplay
    ) { }

    convert(customer: any) {
        if (this.customImportDisplay)
            return this.customImportDisplay.convert(customer);
        else {
            if (StringUtils.isEmpty(customer.LastName)) {
                customer.LastName = customer.FirstName;
                customer.FirstName = '';
            }
            return customer;
        }
    }
}