import { Component, OnInit, Output, Input, EventEmitter, Optional, Inject, OnDestroy } from '@angular/core';
import { MetaService, Language, MetaController, FormMetaComponent, ProfileCodeService, DefaultMetaParser, InputExecutor, MetaColumn } from '@allianzSND/core';
import { CustomerFilterCriteria } from '../bean/customer-filter-criteria';
import { Subject } from 'rxjs';
import { customerFilterMetaControllerToken } from '../../injectionToken/injection-token';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-customer-filter',
  templateUrl: './customer-filter.component.html',
  styleUrls: ['./customer-filter.component.scss']
})
export class CustomerFilterComponent extends FormMetaComponent implements OnInit, OnDestroy {




  //subject to subscribe for open & save & clear
  @Input()
  public clear: Subject<any>;

  @Input()
  public save: Subject<any>;

  @Input()
  public open: Subject<any>;



  //Input criteria
  private _criteria = new CustomerFilterCriteria();
  @Input()
  get criteria() {
    return this._criteria;
  }

  set criteria(criteria: CustomerFilterCriteria) {
    console.log("set criteria:", criteria);
    console.log("PresetJson:", criteria.toMetaJSON());
    this._data = this.convertCriteriaToObject(criteria);
    this.waitUntilMetaLoaded().then(() => {
      console.log("this.metaConfig:", this.metaConfig);
      this.onDataUpdated();
    })
  }

  get data() {
    return this._data;
  }

  get columnConfig(): Array<MetaColumn> {
    return this.metaConfig.Columns;
  }

  get footerConfig(): Array<MetaColumn> {
    return this.metaConfig.Footer;
  }


  @Output()
  private doneCriteria: EventEmitter<CustomerFilterCriteria> = new EventEmitter();




  constructor(
    protected metaService: MetaService,
    profileCodeService: ProfileCodeService,
    metaParser: DefaultMetaParser,
    metaExecutor: InputExecutor,
    @Optional() @Inject(customerFilterMetaControllerToken) private customMetaController: MetaController
  ) {
    super(metaService, profileCodeService, metaParser, metaExecutor);
  }

  public language: Language = new Language();
  private unsubscribe$: Subject<void> = new Subject();

  getMetaParams() {
    return null;
  }

  ngOnInit() {
    super.ngOnInit();
    if (this.clear) {
      this.clear
        .pipe(takeUntil(this.unsubscribe$))
        .subscribe(() => {
          this.clearCriteria();
        })
    }

    if (this.save) {
      this.save
        .pipe(takeUntil(this.unsubscribe$))
        .subscribe(() => {
          this.saveCriteria();
        })
    }
  }

  ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  btnClick(type: string, id: string) {
    if (this.customMetaController) {
      this.customMetaController.btnClick(type, id, this._data);
    }
  }


  onValidateAll() {
    if (this.customMetaController)
      return this.customMetaController.onValidateAll(this._data, this.validationResult);
    else
      return true;
  }

  onValueChange(column: string, value: any, groupId = null, index = -1) {
    if (this.customMetaController) {
      this.customMetaController.onValueChange(column, value, groupId, index, this._data, this.validationResult);
    }
    else {
      if (Array.isArray(this._data[column])) {
        if (this._data[column].indexOf(value) > -1) {
          this._data[column] = this._data[column].filter(x => x !== value);
        }
        else {
          this._data[column] = [...this._data[column], value];
        }

        this._data["AsPreset"] = false;
      }
      else {
        this._data[column] = value;
      }
    }
    console.log("this.Data:", this._data);
  }

  getMetaID() {
    return 'customerFilter'
  }

  onDataUpdated() {
    super.onDataUpdated();
    this.fillDefaultVal();
    console.log("Filter Data Updated:", this._data);
    if (this.customMetaController) {
      this.customMetaController.onDataUpdated(this._data);
    }
  }

  getCheckboxValue(column: string, value: string): boolean {
    return this._data[column] && this._data[column].indexOf(value) > -1;
  }


  private fillDefaultVal() {
    this._data = Object.assign(this.getDefaultData(), this._data);
  }

  private getDefaultData() {
    let defaultObj = this.metaConfig.Columns.map(x => x.id).reduce((acc, cur) => {
      acc[cur] = [];
      return acc;
    }, {});
    let footerObj = this.metaConfig.Footer.filter(x => x.type === 'Checkbox').map(x => x.id).reduce((acc, cur) => {
      acc[cur] = false;
      return acc;
    }, {});
    return Object.assign(defaultObj, footerObj);
  }

  private clearCriteria() {
    this._data = Object.assign(this._data, this.getDefaultData());
  }


  private saveCriteria() {
    this.doneCriteria.emit(this.convertToCriteria());
  }


  private convertCriteriaToObject(criteria: CustomerFilterCriteria): any {
    return Object.assign({}, criteria.toMetaJSON());
  }

  private convertToCriteria() {
    let criteria = new CustomerFilterCriteria();
    this.metaConfig.Columns.map(x => x.id).forEach(colID => {
      if (this._data[colID].length > 0)
        criteria.addCriteriaCols(colID, this._data[colID]);
    });
    this.metaConfig.Footer.map(x => x.id).forEach(colID => {
      criteria.setOption(colID, this._data[colID]);
    });

    return criteria;
  }
}
