import { CustomerListAPI } from '../api/CustomerListAPI';
import { CustomerBirthdayListAPI } from '../api/CustomerBirthdayListAPI';
import { CustomerDetailAPI } from '../api/CustomerDetailAPI';
import { CustomerContactNoteAPI } from '../api/CustomerContactNoteAPI';
