import { ImportContactAPI } from '../api/ImportContactAPI';
import { CustomerDeleteContactNoteAPI } from '../api/CustomerDeleteContactNote';
import { CustomerFilterCriteria } from '../components/bean/customer-filter-criteria';
import { CustomerDeleteAPI } from '../api/CustomerDeleteAPI';
