import { IAPI, IMockAPI, DaoFactory, SQLiteTable, ClientCustomDao, ISQLiteAPI, EqualRestriction } from "@allianzSND/core";
import { Observable } from 'rxjs';
import { CustomerFilterCriteria } from "../components/bean/customer-filter-criteria";

export class CustomerSavePresetAPI implements IAPI, IMockAPI, ISQLiteAPI {

    private _criteria: CustomerFilterCriteria;

    constructor(private daoFactory: DaoFactory) {

    }

    setFilterCriteria(criteria: CustomerFilterCriteria) {
        this._criteria = criteria;
    }

    getAPIName(): string {
        return 'saveCustomerFilterPreset';
    }

    getMockPath(): string {
        return './assets/mock/saveSuccess.json';
    }

    executeSQL(): Observable<any> {
        
        return Observable.create((observer) => {
            let dao = this.daoFactory.getDefaultDao();
            if (dao != undefined) {

                //save customer data
                let setting = this.daoFactory.getDefaultTable("TW_LH_SD_Personal_Setting");
                if (setting) {
                    dao = new ClientCustomDao(dao);

                    setting = (<SQLiteTable>setting);

                    setting.addRestriction(new EqualRestriction('SettingID',['CustomerFilterSetting']));
                    setting.setValue("SettingVal",JSON.stringify(this._criteria.toPresetJSON()));

                    dao.updateByTable(setting).subscribe((resp) => {
                        observer.next(resp);
                        observer.complete();
                    });
                }
                else {
                    observer.next(false);
                    observer.complete();
                }

            }
            else {
                observer.next(false);
                observer.complete();
            }
        });
    }
}
