import { StringUtils } from "@allianzSND/core";
import { getMonth, getDate } from 'date-fns';

export class CustomerBirthday{
    
    private _clientID : string;
    public lastName : string;
    public firstName : string;
    public birthdayMonth : string;
    public birthdayDate : string;

    constructor(clientID : string,firstName : string ,lastName : string, birthdayMonth: string , birthdayDate : string) {

        this._clientID = clientID;
        this.firstName = firstName;
        this.lastName = lastName;
        this.birthdayMonth = birthdayMonth;
        this.birthdayDate =  birthdayDate;
       

         if(StringUtils.isEmpty(this.firstName)) this.firstName = '';

    }

    clone(): CustomerBirthday{
        return new CustomerBirthday(this._clientID, this.firstName, this.lastName, this.birthdayMonth, this.birthdayDate);
    }


   

   
}