import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectorRef, Optional, Inject, ViewChildren, QueryList } from '@angular/core';
import { DateUtils, StringUtils, Language, MetaService, ProfileCodeService, InputExecutor, DefaultMetaParser, MetaColumn, MetaController } from '@allianzSND/core';
import { showRuleToken, DisplayMetaComponent } from '@allianzSND/core';
import { showRule } from '@allianzSND/core';
import { CustomerUtils } from '../../utils/customer-utils';
import { customerDetailMetaControllerToken } from '../../injectionToken/injection-token';

@Component({
  selector: 'app-customer-detail',
  templateUrl: './customer-detail.component.html',
  styleUrls: ['./customer-detail.component.scss']
})
export class CustomerDetailComponent extends DisplayMetaComponent implements OnInit {

  @Output()
  private onEditDetail: EventEmitter<string> = new EventEmitter();
  @Output()
  private onCallPhone: EventEmitter<string> = new EventEmitter();
  @Output()
  private onDeleteDetail: EventEmitter<string> = new EventEmitter();
  @Output()
  private onAddAppointment: EventEmitter<string> = new EventEmitter();
  @Output()
  private followChange: EventEmitter<{ 'isFollow': boolean, 'clientID': string }> = new EventEmitter();




  public DisplayData = null;
  public readonly dash: string = '- -';

  @Input()
  get data() {
    return this._data;
  }
  set data(value) {
    this._data = value;
    console.log("detail Set Data:", value);
    if (value && value.ClientID) {
      this.waitUntilMetaLoaded().then(() => {
        this.onDataUpdated();
      })
    }
    else {
      this._data = null;
    }
  }

  protected getMetaID() {
    return 'customerDetail';
  }

  protected getMetaParams() {
    return null;
  }

  get rowMetaWithoutGroup(): Array<Array<MetaColumn>> {
    return this.metaConfig.Rows.filter(x => x.map(y => y.type !== 'Group').reduce((acc, cur) => acc && cur, true));
  };

  get rowMetaGroup(): Array<MetaColumn> {
    return this.metaConfig.Columns.filter(x => x.type === 'Group');
  }

  get followStatus() {
    return this._data["IsFollow"] === 'Y'
  }


  public language: Language = new Language();

  // empty status
  public isEmptyAppointment: boolean = false;
  public isEmptyEdit: boolean = false;
  public isEmptyDel: boolean = false;
  public isEmptyDetailInfo: boolean = false;
  public isEmptyGroupDetailInfo: boolean = false;
  public isEmptyContactNote: boolean = false;
  public isCollapseBtnShow: boolean = true;

  // card2 extend template is null or not
  public isCardExtendTemplateNull = true;
  @ViewChildren('detailPostTemp') detailPostTemp: QueryList<any>;
  //card2 collapse isOpen or not
  public isCollapseOpen = false;

  public get isHasAgeRange() {
    return this._data && !!this._data["AgeRange"];
  }

  public get isHasContactFrequancy() {
    return this._data && !!this._data["ContactFrequancy"];
  }





  constructor(
    private dateUtils: DateUtils,
    protected profileCodeService: ProfileCodeService,
    private changeDetctor: ChangeDetectorRef,
    protected metaService: MetaService,
    private customerUtils: CustomerUtils,
    inputExecutor: InputExecutor,
    metaParser: DefaultMetaParser,
    @Optional() @Inject(showRuleToken) private showRule: showRule,
    @Optional() @Inject(customerDetailMetaControllerToken) private customMetaController: MetaController
  ) {
    super(metaService, profileCodeService, metaParser, inputExecutor);
  }
  ngOnInit() {
    console.log("customerDetail ngOninit!");
    super.ngOnInit();
  }


  onDataUpdated() {
    console.log("customerDetail onDataUpdated!", this._data);
    super.onDataUpdated();

    //check datasource is from OPUS
    this.isEmptyDel = this._data.DataSource == 'OPUS';

    this.convertEmptyValToDisplayText(this._data);
    this.checkInfoIsEmpty();
    this.changeCollateButton();

    this.DisplayData = Object.assign({}, this._data);
    if (this.customMetaController)
      this.customMetaController.onDataUpdated(this._data);
    this.changeDetctor.detectChanges();
    console.log("after customerDetail onDataUpdated!", this.DisplayData);
  }




  private changeCollateButton() {
    console.log("changeCollateButton Meta:", this.metaConfig);
    this.isCollapseBtnShow = this.metaConfig.Columns.filter(x => x.type === "Group")
      .map(x => this._data[x.id].length > (12 / x.grid.pc))
      .reduce((acc, cur) => acc || cur, false);

    console.debug('isCollapseBtnShow', this.isCollapseBtnShow);
  }



  private checkInfoIsEmpty() {
    //if detail info is empty , display no data status    
    this.isEmptyDetailInfo =
      this.metaConfig.Columns.filter(x => x.type !== "Group")
        .map(x => this._data[x.id] === null || this._data[x.id] === undefined || StringUtils.isEmpty(this._data[x.id]) || this._data[x.id] === this.dash)
        .reduce((acc, cur) => acc && cur, true);

    this.isEmptyGroupDetailInfo = this.metaConfig.Columns.filter(x => x.type === "Group")
      .map(x => this._data[x.id].length === 0)
      .reduce((acc, cur) => acc && cur, true);

    console.log("isEmptyDetailInfo:", this.isEmptyDetailInfo);
    console.log("isEmptyGroupDetailInfo:", this.isEmptyGroupDetailInfo);
  }

  private convertEmptyValToDisplayText(customerDetail: any) {
    //if not data set default value(?? or -- --)
    console.log("convertEmptyValToDisplayText: ", customerDetail);
    customerDetail.AgeRange = this.profileCodeService.convertCode2Text("Customer_Age", customerDetail.AgeRange);
    customerDetail.ContactFrequancy = this.profileCodeService.convertCode2Text("Customer_ContactFrequancy", customerDetail.ContactFrequancy);
    customerDetail.showName = this.convertNameToShow(customerDetail.FirstName, customerDetail.LastName);
    customerDetail.address.forEach(addr => {
      addr["AddressDisplay"] = this.toFullAddress(Object.assign({}, addr));
    });
    this.metaConfig.Columns.filter(x => x.type !== "Group").forEach(col => {
      if (col.id === 'BirthdayDisplay')
        customerDetail.BirthdayDisplay = StringUtils.isNotEmpty(customerDetail.Birthday) ? this.toBirthdayStr() : this.dash;
      else
        customerDetail[col.id] = StringUtils.isEmpty(customerDetail[col.id]) ? this.dash : customerDetail[col.id];
    });
  }

  private toBirthdayStr() {

    if (this._data.Birthday !== undefined) {
      if (this.showRule) {
        return this.showRule.convertDate(new Date(this._data.Birthday));
      }
      else {
        return this.dateUtils.toDateString(new Date(this._data.Birthday), 'yyyy-MM-dd');
      }
    }
  }

  private toFullAddress(addressObj: any): string {
    if (this.showRule)
      return this.showRule.convertAddress(addressObj);
    else {
      let array = [];
      if (StringUtils.isNotEmpty(addressObj.Country)) array.push(addressObj.Country);
      if (StringUtils.isNotEmpty(addressObj.City)) array.push(addressObj.City);
      if (StringUtils.isNotEmpty(addressObj.Area)) array.push(addressObj.Area);
      if (StringUtils.isNotEmpty(addressObj.Zipcode)) array.push(addressObj.Zipcode);
      if (StringUtils.isNotEmpty(addressObj.Address)) array.push(addressObj.Address);
      return array.join(', ');
    }
  }

  edit() {
    this.onEditDetail.emit(this._data.ClientID);
  }

  delete() {
    this.onDeleteDetail.emit(this._data.ClientID);
  }

  callPhone() {
    this.onCallPhone.emit(this._data.ClientID);
  }

  addAppointment() {
    this.onAddAppointment.emit(this._data.ClientID);
  }

  trackByFn(index, item) {
    return item.name;
  }


  isFollowChange(isFollow: boolean) {
    console.debug('isFollowChange', isFollow);
    // display new follow state first
    this._data["IsFollow"] = isFollow ? "Y" : "N";
    this.changeDetctor.detectChanges();

    //ouput follow detail change status
    this.followChange.emit({ 'isFollow': isFollow, "clientID": this._data.ClientID });


  }

  private convertNameToShow(firstName: string, lastName: string): string {
    return this.customerUtils.convertNameToShow(firstName, lastName);
  }

}
