import { ContactItem } from "@allianzSND/core";

export class CustomerImportGroup {
    private _groupName : string;
    private _isShow : boolean = true;
    private _items : Array<ContactItem> = new Array<ContactItem>();

    constructor(groupName : string) {
        this._groupName = groupName;
    }

    public addItem(item : ContactItem) {
        this._items.push(item);
    }

    public get getItems() {
        return this._items;
    }

    public get groupName() {
        return this._groupName;
    }

    public set groupName(groupName : string) {
        this._groupName = groupName;
    }

    public get isShow() {
        return this._isShow;
    }

    public set isShow(isShow : boolean) {
        this._isShow = isShow;
    }

}