import { IAPI, IMockAPI, DaoFactory, SQLiteTable, ClientCustomDao, ISQLiteAPI, EqualRestriction } from "@allianzSND/core";
import { Observable } from 'rxjs';

export class CustomerUpdateFollowAPI implements IAPI, IMockAPI, ISQLiteAPI {

    private _clientID : string;
    private _isFollow : boolean;

    constructor(private daoFactory: DaoFactory) {

    }

    setClient(clientID : string) {
        this._clientID = clientID;
    }

    setIsFollow(isFollow : boolean) {
        this._isFollow = isFollow;
    }

    getAPIName(): string {
        return 'updateCustomerFollowStatus';
    }

    getMockPath(): string {
        return './assets/mock/saveSuccess.json';
    }

    executeSQL(): Observable<any> {
        
        return Observable.create((observer) => {
            let dao = this.daoFactory.getDefaultDao();
            if (dao != undefined) {

                //save customer data
                let customer = this.daoFactory.getDefaultTable("TW_LH_SD_Customer");
                if (customer) {
                    dao = new ClientCustomDao(dao);

                    customer = (<SQLiteTable>customer);

                    customer.addRestriction(new EqualRestriction('ClientID',[this._clientID]));
                    customer.setValue("IsFollow",this._isFollow ? 'Y' : 'N');

                    dao.updateByTable(customer).subscribe((resp) => {
                        
                        observer.next(resp);
                        observer.complete();
                    });
                }
                else {
                    observer.next(false);
                    observer.complete();
                }

            }
            else {
                observer.next(false);
                observer.complete();
            }
        });
    }
}
