export class MobileCustomerItem{

    private lastName : string;
    private firstName : string;
    private phoneNumber : Array<{id:number,text:string}>;
    private email : Array<string>;
    private address : Array<{id:number,city:string,area:string,code:number,address:string}>;
    private birthday : string;

    constructor(lastName : string,firstName : string,phoneNumber : Array<{id:number,text:string}>,email : Array<string>,address : Array<{id:number,city:string,area:string,code:number,address:string}>,birthday : string){
        
        this.lastName = lastName;
        this.firstName = firstName;
        this.phoneNumber = phoneNumber;
        this.email = email;
        this.address = address;
        this.birthday = birthday;
    }

}