import { IAPI, IMockAPI, DaoFactory, SQLiteTable, ClientCustomDao, ISQLiteAPI, EqualRestriction } from "@allianzSND/core";
import { Observable } from 'rxjs';
export class CustomerGetPresetAPI implements IAPI, IMockAPI, ISQLiteAPI {

    constructor(private daoFactory: DaoFactory) {

    }

    getAPIName(): string {
        return 'getCustomerFilterPreset';
    }

    getMockPath(): string {
        return './assets/mock/getCustomerFilterPreset.json';
    }

    executeSQL(): Observable<any> {
        
        return Observable.create((observer) => {
            let dao = this.daoFactory.getDefaultDao();
            if (dao != undefined) {

                //save customer data
                let setting = this.daoFactory.getDefaultTable("TW_LH_SD_Personal_Setting");
                if (setting) {
                    dao = new ClientCustomDao(dao);

                    setting = (<SQLiteTable>setting);

                    setting.addRestriction(new EqualRestriction('SettingID',['CustomerFilterSetting']));

                    dao.queryByTable(setting).subscribe((resp) => {
                        console.debug(resp);
                        observer.next(resp);
                        observer.complete();
                    });
                }
                else {
                    observer.next(false);
                    observer.complete();
                }

            }
            else {
                observer.next(false);
                observer.complete();
            }
        });
    }
}
