import { IAPI, IMockAPI, DaoFactory, ClientCustomDao, ISQLiteAPI, EqualRestriction } from "@allianzSND/core";
import { Observable } from "rxjs";

export class CustomerTelAPI implements IAPI , IMockAPI , ISQLiteAPI {

  private clientID : string;

  constructor(private daoFactory: DaoFactory) {

  }

  setClientID(id : string) {
      this.clientID = id;
  }

  getAPIName() : string {
      return 'getCustomerTel';
  }

  getMockPath() : string {
      return './assets/mock/getCustomerTel.json';
  }
  
  executeSQL(): Observable<any> {
    return Observable.create((observer) => {
      let telObj = this.daoFactory.getDefaultTable("TW_LH_SD_Customer_Tel");
      let dao = this.daoFactory.getDefaultDao();
      if (telObj != undefined && dao != undefined) {
        
        dao = new ClientCustomDao(dao);

        telObj.addRestriction(new EqualRestriction('CustomerClientID',[this.clientID]));
      
        dao.queryByTable(telObj).subscribe((resp) => {
          console.debug(resp);
          observer.next(resp);
          observer.complete();
        });
      } else {
        observer.next(false);
        observer.complete();
      }
    });
  }
}