import { IAPI, IMockAPI, DaoFactory, ClientCustomDao, ISQLiteAPI, EqualRestriction } from "@allianzSND/core";
import { Observable } from 'rxjs';

export class CustomerDeleteAPI implements IAPI , IMockAPI, ISQLiteAPI {
  private _clientID: string;

  constructor(private daoFactory: DaoFactory) {

  }
  
  set clientID(clientID: string) {
    this._clientID = clientID;
  }

  getAPIName() : string {
    return 'deleteCustomer';
  }

  getMockPath() : string {
    return './assets/mock/saveSuccess.json';
  }

  executeSQL(): Observable<any> {
    return Observable.create((observer) => {
      let customerObj = this.daoFactory.getDefaultTable("TW_LH_SD_Customer");
      let dao = this.daoFactory.getDefaultDao();
      if (customerObj != undefined) {
        
        dao = new ClientCustomDao(dao);
        customerObj.addRestriction(new EqualRestriction('ClientID',[this._clientID]));
        dao.deleteByTable(customerObj).subscribe((resp) => {    
          observer.next(resp);
          observer.complete();
        });

      } else {
        observer.next(false);
        observer.complete();
      }
    });
  }
}
