import { MetaController, ValidationResult } from "@allianzSND/core";
import { Injectable } from "@angular/core";

@Injectable({
    providedIn: "root"
})
export class DefaultCustomerEditMetaController implements MetaController {

    onDataUpdated(data: any) {
        if (data['tel']) {
            data['notOPUSTel'] = data['tel'].filter(x => x.DataSource && x.DataSource !== 'OPUS');
        }
        console.log("Default controller onDataUpdated:", data);
    }

    onValueChange(column: string, value: any, groupId: string, index: number, data: any, validationResult: ValidationResult) {
        // if (column === 'Email') {
        //     let isEmail = this.isEmailFormat(value);
        //     if (!isEmail)
        //         validationResult.setErrorMap(column + index.toString(), 'Wrong_Email');
        //     else
        //         validationResult.deleteError(column + index.toString());
        // }
    }

    onValidateAll(data: any, validationResult: ValidationResult) {
        data['tel'] = data['tel'].filter(x => x.dataSource && x.dataSource === 'OPUS').concat(data['notOPUSTel']);
        if (data['email'] && data['email'].length > 0) {
            data['email'].forEach((ele, index) => {
                let isEmail = this.isEmailFormat(ele.Email);
                if (!isEmail)
                    validationResult.setErrorMap('Email' + index.toString(), 'Wrong_Email');
                else
                    validationResult.deleteError('Email' + index.toString());
            });
        }
        return validationResult.isTrue();
    }

    btnClick(type: string, id: string, data: any) { }


    private isEmailFormat(email) {
        let regexp = new RegExp(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);

        return regexp.test(email);
    }


}