import { IAPI, SQLiteResponse } from "@allianzSND/core";
import { IMockAPI } from "@allianzSND/core";
import { ISQLiteAPI } from '@allianzSND/core';
import { Observable, of } from 'rxjs';
import { DaoFactory } from '@allianzSND/core';
import { ClientCustomDao } from '@allianzSND/core';
import { EqualRestriction } from '@allianzSND/core';

export class CalendarEventDetailAPI implements IAPI, IMockAPI, ISQLiteAPI {
  private _clientID: string;
  private _DaoFactory: DaoFactory;
  constructor(DaoFactory) {
    this._DaoFactory = DaoFactory;
  }


  set ClientID(clientID: string) {
    this._clientID = clientID;
  }

  getAPIName(): string {
    return 'getCalendarEventDetail';
  }

  getMockPath(): string {
    return './assets/mock/getCalendarEventDetail.json';
  }

  executeSQL(): Observable<any> {
    return Observable.create((observer) => {

      console.log("CalendarEventDetail ClientID:", this._clientID);

      let defaultObj = {
        ClientID: null,
        CalendarID: null,
        CustomerClientID: null,
        Title: '',
        Location: '',
        CalendarType: null,
        IsAllDay: 'N',
        StartTime: null,
        EndTime: null,
        IsAlert: 'Y',
        Alert1: '8',
        Alert2: '',
        Alert3: null,
        Remark: '',
        DataSource: 'APP'
      };
      if (!this._clientID) {
        //Add
        let resp = new SQLiteResponse({
          "isSuccess": true
        }, [defaultObj]);
        observer.next(resp);
        observer.complete();
      }
      else {
        let calendarObj = this._DaoFactory.getDefaultTable("TW_LH_SD_VW_Calendar");
        let dao = this._DaoFactory.getDefaultDao();
        if (calendarObj != undefined && dao != undefined) {

          dao = new ClientCustomDao(dao);
          calendarObj.addRestriction(new EqualRestriction('ClientID', [this._clientID]));
          dao.queryByTable(calendarObj).subscribe((resp) => {
            observer.next(resp);
            observer.complete();
          });
        } else {
          observer.next(false);
          observer.complete();
        }
      }
    });
  }
}
