import { Component, OnInit, Input, Inject, Optional, Output, EventEmitter, OnDestroy } from '@angular/core';
import { MetaService, MetaController, FormMetaComponent, ProfileCodeService, DefaultMetaParser, InputExecutor, MetaColumn } from '@allianzSND/core';
import { customerContactEditMetaControllerToken } from '../../injectionToken/injection-token';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-customer-contact-edit',
  templateUrl: './customer-contact-edit.component.html',
  styleUrls: ['./customer-contact-edit.component.scss']
})
export class CustomerContactEditComponent extends FormMetaComponent implements OnInit, OnDestroy {


  @Input()
  public set data(value) {
    let defaultObj = {};
    if (this.metaConfig)
      defaultObj = this.metaConfig.Columns.map(x => x.id).reduce((acc, current) => {
        acc[current] = '';
        return acc;
      }, {});
    if (value != null)
      this._data = Object.assign(defaultObj, value);
    else
      this._data = defaultObj;
    console.log("set contact edit data:", this._data);
    if (this.customMetaController)
      this.customMetaController.onDataUpdated(this._data);
    this.customerClientID = this._data.CustomerClientID;
  }

  public get data() {
    return this._data;
  }

  get columnConfig(): Array<MetaColumn> {
    return this.metaConfig.Columns;
  }

  private customerClientID = '';
  private unsubscribe$: Subject<void> = new Subject<void>();

  @Input() saveSubject: Subject<any>;
  @Output() save: EventEmitter<any> = new EventEmitter();

  constructor(
    metaService: MetaService,
    profileCodeService: ProfileCodeService,
    metaParser: DefaultMetaParser,
    metaExecutor: InputExecutor,
    @Optional() @Inject(customerContactEditMetaControllerToken) private customMetaController: MetaController
  ) {
    super(metaService, profileCodeService, metaParser, metaExecutor);
  }

  ngOnInit() {
    super.ngOnInit();
    if (this.saveSubject) {
      this.saveSubject
        .pipe(takeUntil(this.unsubscribe$))
        .subscribe(() => {
          this.onBtnClick('submit', 'save');
        })
    }
  }

  ngOnDestroy() {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  getMetaParams() {
    return {
      CustomerClientID: this.customerClientID
    }
  }

  btnClick(type: string, id: string) {
    if (type === 'submit') {
      this.save.emit(this._data);
    }
    if (this.customMetaController)
      this.customMetaController.btnClick(type, id, this._data);

  }
  getMetaID() {
    return 'customerContactEdit';
  }
  onValidateAll() {
    if (!this.validationResult.isTrue()) {
      //If basic validation has error.
      if (this.validationResult.isError('Note'))
        alert("Note " + this.validationResult.getErrorMsg("Note"));
      return false;
    }
    else {
      //Do custom validation.
      let result = true;
      if (this.customMetaController)
        result = result && this.customMetaController.onValidateAll(this._data, this.validationResult);
      return result;
    }
  }
  onValueChange(column: string, value: any, groupId: string, index: number) {
    //Trigger other column values
    if (this.customMetaController)
      this.customMetaController.onValueChange(column, value, groupId, index, this._data, this.validationResult);
  }

}
