import { Component, OnInit, Input, Output, EventEmitter, Optional, Inject } from '@angular/core';
import { ContactItem, Language, showRuleToken, showRule } from '@allianzSND/core';
import { CustomerImportGroup } from '../bean/customer-import-group';

@Component({
  selector: 'app-customer-import',
  templateUrl: './customer-import.component.html',
  styleUrls: ['./customer-import.component.scss']
})
export class CustomerImportComponent implements OnInit {

  @Input()
  public importContractMap = new Map<string, CustomerImportGroup>();

  @Output()
  private importCustomer: EventEmitter<Array<ContactItem>> = new EventEmitter();


  public language: Language = new Language();

  constructor(
    @Optional() @Inject(showRuleToken) private showRule: showRule
  ) { }

  ngOnInit() {

  }


  doImport() {

    let importItems: Array<ContactItem> = new Array<ContactItem>();

    this.importContractMap.forEach((group: CustomerImportGroup, groupName: string) => {
      group.getItems.forEach((item: ContactItem) => {
        console.debug(item);
        if (item.isCheck) {
          importItems.push(item);
        }
      })
    });

    this.importCustomer.emit(importItems);
  }

  toDisplayName(firstname: string, lastname: string) {
    if (this.showRule)
      return this.showRule.convertName(firstname, lastname);
    else
      return `${lastname} ${firstname}`;
  }

}
