import { IAPI, IMockAPI, DaoFactory, SQLiteTable, ClientCustomDao, ISQLiteAPI, EqualRestriction } from "@allianzSND/core";
import { Observable } from 'rxjs';

export class DashboardUpdateToReadAPI implements IAPI, IMockAPI, ISQLiteAPI {

    private _messageType : string;
    private _messageDataCategory : string;

    constructor(private daoFactory: DaoFactory) {

    }

    public setMessageType(type) {
        this._messageType = type;
    }

    public setMessageDataCategory(dataCategory) {
        this._messageDataCategory = dataCategory;
    }



    getAPIName(): string {
        return 'updateDashboardToRead';
    }

    getMockPath(): string {
        return './assets/mock/saveSuccess.json';
    }

    executeSQL(): Observable<any> {
        
        return Observable.create((observer) => {
            let dao = this.daoFactory.getDefaultDao();
            if (dao != undefined) {

                //save customer data
                let messageObj = this.daoFactory.getDefaultTable("TW_LH_SD_Message");
                if (messageObj) {
                    dao = new ClientCustomDao(dao);

                    messageObj = (<SQLiteTable>messageObj);

                    messageObj.addRestriction(new EqualRestriction('MessageCategory',[this._messageDataCategory]));
                    messageObj.addRestriction(new EqualRestriction('MessageType',[this._messageType]));
                    messageObj.setValue("Status", 'Reading');

                    dao.updateByTable(messageObj).subscribe((resp) => {
                        
                        observer.next(resp);
                        observer.complete();
                    });
                }
                else {
                    observer.next(false);
                    observer.complete();
                }

            }
            else {
                observer.next(false);
                observer.complete();
            }
        });
    }
}
