import { IAPI, IMockAPI, DaoFactory, ClientCustomDao, ISQLiteAPI, EqualRestriction } from "@allianzSND/core";
import { Observable } from 'rxjs';

export class CustomerEditContactNoteAPI implements IAPI , IMockAPI, ISQLiteAPI {
  private contactClientID:  string;
  private note : string;
  private noteTime : Date;

  constructor(private daoFactory: DaoFactory) {

  }

  setContactClientID(contactClientID : string) {
    this.contactClientID = contactClientID;
  }

  setNote(note : string) {
    this.note = note;
  }

  setNoteTime(noteTime : Date) {
    this.noteTime = noteTime;
  }

  getAPIName() : string {
    return 'editCustomerContactNote';
  }

  getMockPath() : string {
    return './assets/mock/saveSuccess.json';
  }

  executeSQL(): Observable<any> {
    return Observable.create((observer) => {
      let contactObj = this.daoFactory.getDefaultTable("TW_LH_SD_Customer_Contact");
      let dao = this.daoFactory.getDefaultDao();
      if (contactObj != undefined && dao != undefined) {
        
        dao = new ClientCustomDao(dao);

        contactObj.addRestriction(new EqualRestriction('ClientID',[this.contactClientID]));
        contactObj.setValue('Note', this.note);
        contactObj.setValue('NoteTime', this.noteTime.getTime());
      
        dao.updateByTable(contactObj).subscribe((resp) => {
          observer.next(resp);
          observer.complete();
        });
      } else {
        observer.next(false);
        observer.complete();
      }
    });
  }
}
