import { Component, OnInit, Input, Output, EventEmitter, ElementRef, Inject, Optional } from '@angular/core';
import { DateUtils, Language, showRuleToken, showRule } from '@allianzSND/core';

@Component({
  selector: 'app-customer-contact-list',
  templateUrl: './customer-contact-list.component.html',
  styleUrls: ['./customer-contact-list.component.scss']
})
export class CustomerContactListComponent implements OnInit {


  public language: Language = new Language();

  @Output()
  private onAddNote: EventEmitter<any> = new EventEmitter();

  @Output()
  private onDisplayNote: EventEmitter<any> = new EventEmitter();

  @Output()
  private onEditNote: EventEmitter<any> = new EventEmitter();

  @Output()
  private onDeleteNote: EventEmitter<any> = new EventEmitter();

  @Output()
  private contactListRefresh: EventEmitter<any> = new EventEmitter();




  @Input()
  get contactList() { return this._contactList; }
  set contactList(contactList) {
    console.log("customer-contact-list set contactList:", contactList);

    this._contactList = contactList.map(x => Object.assign({}, x, { NoteTimeDisplay: this.toNoteTime(new Date(x.NoteTime)) }));

    setTimeout(() => {
      this.closeSlidingItems();
    }, 200)
  }
  private _contactList: Array<any>;

  elementRef: ElementRef;
  constructor(private dateUtils: DateUtils,
    @Inject(ElementRef) elementRef: ElementRef,
    @Optional() @Inject(showRuleToken) private showRule:showRule
    ) {
    this.elementRef = elementRef;
  }


  ngOnInit() {
  }

  ngAfterViewInit() {
    setTimeout(() => {
      this.closeSlidingItems();
    }, 200)

  }

  // ionic sliding need to call close event when every refresh
  async closeSlidingItems() {
    console.debug('closeSlidingItems');


    const item = this.elementRef.nativeElement.querySelector('ion-item-sliding');
    if (item != null) {
      /// console.debug('item size',item.length);
      console.debug('item', item);
      await item.close();
      await item.closeOpened();
    }

  }

  refreshContactNote() {
    this.contactListRefresh.emit()


    // need to wait ion-item-sliding create at first from no data (when skelton interface)
    setTimeout(() => {
      this.closeSlidingItems();
    }, 200)

  }

  addNote() {
    this.onAddNote.emit();
  }

  displayNote(note: any) {
    this.onDisplayNote.emit(Object.assign({
      Date: this.toNoteTime(new Date(note.NoteTime))
    }, note));
  }

  editNote(note: any) {
    this.onEditNote.emit(note);
  }

  deleteNote(note: any) {
    this.onDeleteNote.emit(note);
  }


  trackByClientID(index, item) {
    return item.ClientID;
  }

  public toNoteTime(time: Date) {
    console.log("toNoteTime:", time);
    let defaultStr = this.dateUtils.toDateString(time, 'yyyy-MM-dd HH:mm');
    if (this.showRule)
      return this.showRule.convertDate(time) + defaultStr.slice(10);
    else
      return defaultStr;
  }
}
