import { StringUtils } from "@allianzSND/core";

export class CustomerItem{
    
    private _clientID : string;
    private _firstName : string;
    private _lastName : string;
    private _showName: string;
    private _tag : string;
    private _complementPercent : number;
    private _isFollow : boolean;
    private _isOtherSource : boolean;
    private _isHighlight : boolean;

    constructor(clientID : string,firstName : string ,lastName : string, possibility : string ,
         complementPercent : number,isFollow : boolean , isOtherSource : boolean , isOverTimeAlert : string) {

        this._clientID = clientID;
        this._firstName = firstName;
        this._lastName = lastName;
        this._tag = possibility;
        this._complementPercent = complementPercent;
        this._isFollow = isFollow;
        this._isOtherSource = isOtherSource;
        
        if(isOverTimeAlert == 'Y') {
            this._isHighlight = true;
        }

        if(StringUtils.isEmpty(this._firstName)) this._firstName = '';
    }


    get isOtherSource() {
        return this._isOtherSource;
    }

    get isFollow() {
        return this._isFollow;
    }

    get clientID(){
        return this._clientID;
    }

    get firstName(){
        return this._firstName;
    }

    get lastName(){
        return this._lastName;
    }

    get tag(){
        return this._tag;
    }

    get complementPercent(){
        return this._complementPercent;
    }

    set clientID(clientID : string){
        this._clientID = clientID;
    }    

    set firstName(name : string){
        this._firstName = name;
    }

    set lastName(name : string){
        this._lastName = name;
    }

    set tag(tag : string){
        this._tag = tag;
    }

    set complementPercent(complementPercent : number){
        this._complementPercent = complementPercent;
    }

    set isFollow(isFollow : boolean) {
        this._isFollow = isFollow;
    }

    set isOtherSource(isOtherSource : boolean) {
        this._isOtherSource = isOtherSource;
    }

    get isHighlight() {
        return this._isHighlight;
    }

    set isHighLight(isHighLight : boolean) {
        this._isHighlight = isHighLight;
    }

    get showName() {
        return this._showName;
    }
    
    set showName(showName: string) {
        this._showName = showName;
    }


    clone():CustomerItem {
        return new CustomerItem(this._clientID, this._firstName, this._lastName, this._tag, this._complementPercent, this._isFollow, this._isOtherSource, (this._isHighlight ? 'Y' : 'N'));
    }
}