import { Component, OnInit, Input, Optional, Inject } from '@angular/core';
import { MetaService, DisplayMetaComponent, ProfileCodeService, DefaultMetaParser, InputExecutor, MetaColumn, MetaController } from '@allianzSND/core';
import { customerContactDetailMetaControllerToken } from '../../injectionToken/injection-token';

@Component({
  selector: 'app-customer-contact-detail',
  templateUrl: './customer-contact-detail.component.html',
  styleUrls: ['./customer-contact-detail.component.scss']
})
export class CustomerContactDetailComponent extends DisplayMetaComponent implements OnInit {
  @Input()
  public set data(value) {
    this._data = value;
    console.log("set contact detail data:", value);
  }

  public get data() {
    return this._data;
  }

  get columnConfig(): Array<MetaColumn> {
    return this.metaConfig.Columns;
  }

  constructor(
    metaService: MetaService,
    profileCodeService: ProfileCodeService,
    metaParser: DefaultMetaParser,
    metaExecutor: InputExecutor,
    @Optional() @Inject(customerContactDetailMetaControllerToken) private customMetaController: MetaController
  ) {
    super(metaService, profileCodeService, metaParser, metaExecutor);
  }

  ngOnInit() {
    super.ngOnInit();
  }

  getMetaID() {
    return 'customerContactDetail';
  }

  getMetaParams() {
    return null;
  }
  onDataUpdated() {
    super.onDataUpdated();
    if (this.customMetaController)
      this.customMetaController.onDataUpdated(this._data);
  }
}
